* {
  box-sizing: border-box;
}

header {
  color: #fff;
  z-index: 1000;
  text-align: center;
  background-color: #081e66;
  border-radius: 0 0 8px 8px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  font-size: 21px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

header h1 {
  margin: 0;
  font-size: 35px;
}

header p {
  margin: 10px 0;
  font-size: 19px;
}

#hamburger-button {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  align-self: flex-end;
  font-size: 24px;
}

#logout-button {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-size: 18px;
  display: none;
}

body {
  background: #dedee1;
  padding-top: 200px;
}

#auth-container {
  background: #f7f8f9;
  border-radius: 8px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

h2 {
  text-align: center;
  color: #081e66;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #081e66;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px 15px;
}

button:hover {
  background-color: #081e66;
}

#account-container {
  background: #fff;
  border-radius: 8px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

#weather-info {
  text-align: center;
  color: #081e66;
}

#transactions-list {
  color: #081e66;
  padding: 0;
  list-style-type: none;
}

@media (width <= 768px) {
  header {
    padding: 15px;
    font-size: 19px;
  }

  #header p {
    font-size: 17px;
  }

  body {
    padding-top: 220px;
  }

  #auth-container {
    max-width: 280px;
  }
}

@media (width <= 370px) {
  header {
    text-align: center;
    padding: 10px;
    font-size: 17px;
  }

  #header p {
    font-size: 15px;
  }

  body {
    padding-top: 220px;
  }

  #auth-container {
    max-width: 100%;
    margin: 35px;
    padding: 15px;
  }

  #logout-button {
    margin-top: 10px;
    font-size: 14px;
    display: none;
  }
}
